<template>
  <div class="card">
    <div class="card-header">
      <strong>{{followers.length}}</strong> followers (追隨者)
    </div>
    <div class="card-body">
      <span
        v-for='follower in followers'
        :key='follower.id'
      >
        <router-link
          :to="{name: 'user-profile',params:{id: follower.id}}">
            <img :src="follower.image" width="60" height="60" class="avatar">
        </router-link>
      </span>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    followers: {
      type: Array,
      required: true
    }
  }
}
</script>